/*
 * react-circular-progressbar styles
 *
 * All of the styles in this file are optional and configurable!
 */

.CircularProgressbar {
  /*
   * This fixes an issue where the CircularProgressbar svg has
   * 0 width inside a "display: flex" container, and thus not visible.
   *
   * If you're not using "display: flex", you can remove this style.
   */
  width: 40%;
}

h3 {
  -webkit-animation: Home-logo-scale2 infinite 8s linear alternate;
          animation: Home-logo-scale2 infinite 8s linear alternate;
}

.CircularProgressbar .CircularProgressbar-path {
  -webkit-animation: Home-logo-scale infinite 12s ease-out alternate;
          animation: Home-logo-scale infinite 12s ease-out alternate;
  stroke: #3e98c7;
  stroke-linecap: round;
  -webkit-transition: stroke-dashoffset 0.5s ease 0s;
  -o-transition: stroke-dashoffset 0.5s ease 0s;
  transition: stroke-dashoffset 0.5s ease 0s;
}

.CircularProgressbar .CircularProgressbar-trail {
  -webkit-animation: Home-logo-scale infinite 12s ease-out alternate;
          animation: Home-logo-scale infinite 12s ease-out alternate;
  stroke: #d6d6d6;
}

.CircularProgressbar .CircularProgressbar-text {
  -webkit-animation: Home-logo-scale infinite 8s linear alternate;
          animation: Home-logo-scale infinite 8s linear alternate;
  fill: #fdb;
  font-size: 20px;
  dominant-baseline: middle;
  text-anchor: middle;
}

@-webkit-keyframes move_eye {
  from { margin-left: -80%; }
  to { margin-left: 80%; }
}

@keyframes move_eye {
  from { margin-left: -80%; }
  to { margin-left: 80%; }
}

@-webkit-keyframes Home-logo-scale {
  from { -webkit-transform: scale(1, 1); transform: scale(1, 1); }
  to { -webkit-transform: scale(.95, .95); transform: scale(.95, .95); }
}

@keyframes Home-logo-scale {
  from { -webkit-transform: scale(1, 1); transform: scale(1, 1); }
  to { -webkit-transform: scale(.95, .95); transform: scale(.95, .95); }
}

@-webkit-keyframes Home-logo-scale2 {
  from { -webkit-transform: scale(.87, .87); transform: scale(.87, .87); }
  to { -webkit-transform: scale(1, 1); transform: scale(1, 1); }
}

@keyframes Home-logo-scale2 {
  from { -webkit-transform: scale(.87, .87); transform: scale(.87, .87); }
  to { -webkit-transform: scale(1, 1); transform: scale(1, 1); }
}



